 <template>
  <div class="block" v-show="!this.$route.query.uid">
    <span class="demonstration">
      <el-table :data="this.$store.getters.getStudentList" style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="userIcon" label="用户头像" align="center" width="100">
          <template slot-scope="scope">
            <div class="demo-image">
            <div class="block">
            <el-image
              :src="scope.row.userIcon"
              alt
              style="width: 5vh;height:5vh;cursor: pointer"
              @click="showBigImg(scope.row.userIcon)"
              @mousedown.prevent
            ></el-image>
            </div></div>
          </template>
        </el-table-column>
        <el-table-column prop="stuName" label="姓名" width="180"></el-table-column>
        <el-table-column prop="gender" label="性别" width="180">
          <template slot-scope="scope">
            <span>{{scope.row.gender ? '男' : '女'}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="stuId" label="学号" width="180"></el-table-column>
        <el-table-column prop="className" label="班级"></el-table-column>
        <el-table-column prop="tellNum" label="电话号码"></el-table-column>
        <!-- 列表操作项 -->
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope"><el-button
              @click="$router.push({path:'student/information',query:{uid:scope.row.userId}})"
              type="text"
              size="small"
            >查看</el-button>
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>

          </template>
        </el-table-column>
      </el-table>
    </span>
    <div style="position:absolute;bottom:20px;left:45%">
      <el-pagination
        :hide-on-single-page="true"
        layout="prev, pager, next"
        :current-page="Number($store.state.student.pageNum)"
        @current-change="getStudentList"
        :total="this.$store.state.student.data.total"
      ></el-pagination>
    </div>
    <el-dialog :visible.sync="bigImg.isShow" width="30%" center>
      <img :src="bigImg.img" alt width="100%" height="100%" />
    </el-dialog>
  </div>
</template>

  <script>
export default {
  inject: ["reload"],
  data() {
    return {
      mgeActVis: false,
      edit: false,
      bigImg: { isShow: false, img: "" },
      activity: ""
    };
  },
  mounted() {
    this.getStudentList();
  },
  methods: {
    handleClick: function(a) {
      console.log(a);
    },
    showBigImg: function(a) {
      this.bigImg.img = a;
      this.bigImg.isShow = !this.bigImg.isShow;
    },
    getStudentList(pageNum = this.$store.state.student.pageNum) {
      //根据活动查找用户
      if (this.$route.query.aid)
        this.$api
          .getUserByAct({
            id: this.$route.query.aid,
            page: { pageNum: pageNum, pageSize: 8, showAll: true }
          })
          .then(data => {
            this.$store.dispatch("pushStudent", {
              name: "student",
              num: pageNum,
              data: data.data
            });
          });
      //查找全部用户
      else
        this.$api
          .getAllUser({ pageNum: pageNum, pageSize: 8, showAll: true })
          .then(data => {
            this.$store.dispatch("pushStudent", {
              name: "student",
              num: pageNum,
              data: data.data
            });
          });
    },
    manageActivity: function(a, data) {
      //管理活动(true为修改,false为新建)
      this.mgeActVis = true;
      a
        ? (() => {
            this.activity = Object.assign({}, data);
            this.edit = true;
          })()
        : (() => {
            this.activity = Object.assign(
              {},
              this.$store.getters.getActivityPrototype
            );
            this.edit = false;
          })();
    },
    deleteActivity: function() {
      //删除活动
      this.$confirm("确定真的要删除该活动吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api
            .delActivity(this.activity.activityId)
            .then(data => {
              let { success, errCode, errMsg } = data;
              if (success) {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.reload();
              } else {
                this.$message({
                  type: "error",
                  message: "删除失败，请重试!"
                });
              }
            })
            .catch(err => {
              console.log(err);
              this.$message({
                type: "error",
                message: "无法连接到服务器!"
              });
            });
        })
        .catch(() => {});
    }
  }
};
</script>


